import { GatsbyImage } from 'gatsby-plugin-image';

import React from 'react';
import styled from 'styled-components';

import { graphql, useStaticQuery } from 'gatsby';




setInterval(() => {
    // console.log('siii');

    const ani1 = document.querySelector('.ani1');
    // mause.style.left = `${x - 10}px`;
    
    const num1 = Math.floor((Math.random() * (-1-100))+101);
    const num2 = Math.floor((Math.random() * (-1-100))+101);
    const num3 = Math.floor((Math.random() * (-1-100))+101);
    const num4 = Math.floor((Math.random() * (-1-100))+101);

    ani1.style.borderRadius = `${num1}% ${num2}% ${num3}% ${num4}% / ${num1}% ${num2}% ${num3}% ${num4}%`;

    // console.log(num);

    // console.log(ani1);
}, 5000);

setInterval(() => {
    // console.log('siii');

    const ani1 = document.querySelector('.ani2');
    // mause.style.left = `${x - 10}px`;
    
    const num1 = Math.floor((Math.random() * (-1-100))+101);
    const num2 = Math.floor((Math.random() * (-1-100))+101);
    const num3 = Math.floor((Math.random() * (-1-100))+101);
    const num4 = Math.floor((Math.random() * (-1-100))+101);

    ani1.style.borderRadius = `${num1}% ${num2}% ${num3}% ${num4}% / ${num1}% ${num2}% ${num3}% ${num4}%`;

    // console.log(num);

    // console.log(ani1);
}, 5000);

const Div = styled.div`
    /* background-color: red; */
    height: 600px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    .conte {
        position: absolute;
        z-index: 999999;
        background-color: #0002;
        /* background-image: linear-gradient(to right, #30373d33, #2a303433, #25292c33, #1f222433, #1a1b1c33); */
        box-shadow: 10px 10px 5px 0px #0006;
        backdrop-filter: blur(20px);
        border-radius: 20px;
        height: 70%;
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        /* justify-content: space-around; */

        /* margin-left: 20px; */
        .info {
            padding: 30px;
            width: 50%;
            height: 100%;
            /* background-color: aqua; */
            display: flex;
            flex-direction: column;
            justify-content: start;

            h1 {
                margin: 10px;
                color: #fff;
            }
            p {
                margin: 10px;
                color: #fff;
            }
        }
        div {
            height: 80%;
            width: 40%;
        }
    }
    @media (max-width: 1000px) {
        div {
            width: 100%;
        }
    }
    .ani1 {
        background-color: #c9b37e;
        border-radius: 39% 61% 58% 42% / 44% 96% 4% 56% ;

        height: 200px;
        width: 200px;
        position: absolute;
        z-index: 99999;
        top: 60%;
        left: 8%;
        transition: 10s;
    }
    .ani2 {
        background-color: #f57878;
        border-radius: 39% 61% 58% 42% / 44% 96% 4% 56% ;

        height: 100px;
        width: 100px;
        position: absolute;
        z-index: 99999;
        top: 10%;
        right: 8%;
        transition: 10s;
    }
    
`;

const Principal = () => {

    const info = useStaticQuery(graphql`
    query {
        datoCmsHome{
          titulo,
          imgmiperfil {
            gatsbyImageData 
          }
          micontenido,
          tec
        }
      }
    `)

    const {titulo, micontenido, imgmiperfil, tec} = info.datoCmsHome;

    // const image = getImage(imgmiperfil);

    const tecno = tec.split(',');

    // console.log(tec);


    return (
        <>
            <Div>
                <div className='conte'>
                    <div className='info'>
                        <h1>{titulo}</h1>
                        <p>{micontenido}</p>

                        {/* <li>{tec}</li> */}
                        {
                            tecno.map( (i) => {
                                console.log(i)
                                return <li>{i}</li>
                            })
                        }
                    </div>
                    <GatsbyImage
                        image={imgmiperfil.gatsbyImageData}
                        alt="imagen"
                    />
                </div>
                <div className='ani1'></div>
                <div className='ani2'></div>
                <div className='ani3'></div>
            </Div>
        </>
    )
}

export default Principal;