window.addEventListener('mousemove', (e) => {
  const mause = document.querySelector('.mause');
  const y = e.pageY;
  const x = e.pageX;
  
  mause.style.left = `${x - 10}px`;
  mause.style.top = `${y - 10}px`;

  // console.log({x});
  // console.log({y});
})


